<template>
  <div class="map-track">
    <div class="map-track__btns">
      <button @click="toggle">{{ state === 'play' ? 'Pause' : 'Play'}}</button>
      Speed: {{ speed }} km/h<br>
    </div>
    <yandex-map
      class="map-track__map"
      :settings="settings"
      :coords="coords"
      @map-was-initialized="mapWasInitialized"
      >
      <ymap-marker
        marker-id="position-marker"
        :coords="markerCoords"
        />
    </yandex-map>
  </div>
</template>

<script>
  import { yandexMap, ymapMarker } from 'vue-yandex-maps'
  import coordinates from '../../mock/coordinates.json'
  import { distanceBetweenLocations } from '../../helpers/map.js'

  const settings = {
    apiKey: '',
    lang: 'ru_RU',
    coordorder: 'latlong',
    version: '2.1'
  }

  export default {
    name: 'MapTrack',
    components: { yandexMap, ymapMarker },
    data () {
      return {
        settings: settings,
        coords: [0, 0],
        index: 0,
        maxPoints: 0, // 0 unlimit
        interval: null,
        intervalSpeed: 50,
        YMap: {},
        state: 'pause',
        step: 1,
        lineColor: '#d00d00'
      }
    },
    computed: {
      formatCoords() {
        const items = this.maxPoints && this.maxPoints < coordinates.length  ? coordinates.slice(0, this.maxPoints) : coordinates
        return items.map( item => {
          return {
            time: item[0],
            lat: item[2],
            lng: item[1]
          }
        })
      },
      cleanCoords () {
        let prev = null
        let maxDiff = 0.05
        return this.formatCoords.filter( item => {
          let res = true
          if(prev) {
            const prevSum = prev.lat + prev.lng
            const nowSum = item.lat + item.lng
            let diff = prevSum - nowSum
            diff = diff > 0 ? diff : diff * -1
            if(diff > maxDiff) {
              let timeDiff = new Date(item.time).getTime() - new Date(prev.time).getTime()
              if(timeDiff < 30000) { // 30 m
                res = false
              }
            }
          }
          prev = item
          return res
        })
      },
      coordsData () {
        return this.cleanCoords
      },
      allLinePoints() {
        return this.coordsData.map( item => {
          return [item.lat, item.lng]
        })
      },
      pointsCount() {
        return this.allLinePoints.length
      },
      markerCoords() {
        return this.allLinePoints[this.index - 1] || this.allLinePoints[0] || []
      },
      speed() {
        const step = 2
        let start = this.index - step
        start = start > 0 ? start : 0
        const items = this.coordsData.slice(start, this.index)
        let distance = 0
        let prev = null
        items.forEach( item => {
          if(prev) {
            distance += (distanceBetweenLocations(prev, item) || 0)
          }
          prev = item
        })
        const startTime = items[0] ? new Date(items[0].time).getTime() : 0
        const endTime = items[items.length - 1] ? new Date(items[items.length - 1].time).getTime() : 0
        const time = startTime && endTime ? endTime - new Date(startTime).getTime() : 0
        let speed = 0
        if(distance && time) {
          const hours = time / (1000 * 60 * 60);
          speed = Math.round(distance / hours)
        }
        return speed
      }
    },
    methods: {
      mapWasInitialized (Map) {
        this.YMap = Map
        this.init()
      },
      init() {
        const allLine = new ymaps.Polyline(this.allLinePoints, {}, {strokeColor: this.lineColor, strokeOpacity: 0.5, strokeWidth: 3 })
        this.YMap.geoObjects.add(allLine)
        this.YMap.setBounds(allLine.geometry.getBounds())
      },
      toggle() {
        if(this.state === 'pause') {
          this.play()
        } else {
          this.pause()
        }
      },
      play() {
        this.state = 'play'
        clearInterval(this.interval)
        if(this.index === this.pointsCount) {
          this.reset()
          this.index = 0
          this.init()
        }
        this.interval = setInterval(() => {
          const next = this.index + this.step
          if( next > this.pointsCount) {
            this.index = this.pointsCount
            this.pause()
          } else {
            this.index = next
          }
        }, this.intervalSpeed)
      },
      pause() {
        this.state = 'pause'
        clearInterval(this.interval)
      },
      reset() {
        this.YMap.geoObjects.each( item => {
          this.YMap.geoObjects.remove(item)
        })
      }
    }
  }
</script>

<style lang="scss">
  .map-track {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    text-align: left;

    &__btns {
      display: flex;
      align-items: center;
      border-bottom: 1px solid #ccc;

      button {
        margin: 10px 15px;
      }
    }

    &__map {
      flex: 1;
    }
  }
</style>
